<template>
  <ConfirmLayer v-if="isConfirm" @closeLayer="cancelCreate">
    <span slot="confirmtitle">{{$t('delproject')}}</span>
    <div slot="confirmcontent">
      <div>
        <p class="text-red-500 text-lg text-left mb-2">{{$t('sureDel')}}</p>
        <p class="text-left mb-2">{{$t('delTips')}}</p>
        <div class="leading-10">
          <div class="flex justify-end items-end flex-wrap mt-6 sm:flex-nowrap"><a
            class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border border-gray-400 text-gray-600 shadow-none hover:bg-gray-200 active:bg-gray-300 mr-4" @click="cancelCreate">{{$t('cancel')}}</a>
            <button
              class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-red-500 border-red-500 hover:bg-red-300 active:bg-red-500"
              type="submit" data-testid="confirm-delete-project-button" @click="deleteProject">{{$t('delete')}}
            </button>
          </div>
        </div>
      </div>
    </div>
  </ConfirmLayer>
</template>

<script>
import mixin from '@mixin';
import ConfirmLayer from '@components/ConfirmLayer.vue';
import layer from '@/utils/layer';
import api from '@/api';

export default {
  name: 'createNew',
  mixins: [mixin],
  props: ['project_id'],
  data() {
    return {
      project_name: '',
    };
  },
  components: {
    ConfirmLayer,
  },
  methods: {
    cancelCreate() {
      this.removeConfirm();
    },
    addProject() {
      this.addConfirm();
    },
    deleteProject() {
      const obj = {
        project_id: this.project_id,
      };
      api.projectApi.delete_project(obj).then((result) => {
        if (!result.success) {
          layer.tip(result.msg, 'error');
        } else {
          layer.tip(this.$t('delsuccess'));
          setTimeout(() => {
            this.cancelCreate();
            // this.$router.go(0)
            this.$router.push('/dashboard/heco');
          }, 1000);
        }
      });
    },
  },
  mounted() {
  },
};
</script>

